    height: 100%;
    width: 100%;
    background: #212226;
    display: inline-block;
}

.footer_subscription {
    width: 100%;
    height: 300px;
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: cover;
    background-attachment: fixed;
    background-color: #212226;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    flex-direction: column;
}

.subscription_wrapper {
    text-align: center;
}

.subscription_title {
    margin: 0 0 30px;
    font-weight: 900;
    text-transform: uppercase;
    font-size: 15px;
    line-height: 20px;
    letter-spacing: .06em;
}

.subscription_container {
    position: relative;
    width: 560px;
    height: 50px;
    overflow: hidden;
    border-radius: 3px;
    background-color: #fff;
