}

.footer_columns {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin: 0 auto;
    position: relative;
    max-width: 1160px;
    align-items: flex-start;
    padding: 40px 0 0;
    border-top: 1px solid #979899;
}

.column_root {
    padding-top: 5px;
    height: auto;
    position: relative;
    width: 180px;
    margin-right: 20px;
}

.title_root {
    margin: 0 0 20px;
    color: #fff;   
    font-weight: 900;
    text-transform: uppercase;
    font-size: 15px;
    line-height: 13px;
    letter-spacing: .06em;
    white-space: nowrap;
}

.column_list {
    list-style-type: none;
    padding-left: 0;
}

.column_item {
    margin-bottom: 5px;
}

.column_item a {
    text-decoration: none;
    outline: 0;
    color: #8b8c8c;
    font-size: 15px;
    transition: color .3s ease;
    will-change: color;
    font-weight: 400;
    display: block;
    line-height: 20px;
}
